<template>
    <div class="items-list">
        <ul class="items">
            <li v-for="(item, id) in data.items"
                :class="{ active: props.id === item.id }"
                @click="emit('change', item)"
            >
                <span class="item-preview">
                    <Img v-if="item.profileImage?.resized || defaultProfileImage?.resized"
                        :preview="item.profileImage?.resized || defaultProfileImage?.resized"
                        :original="item.profileImage?.original || defaultProfileImage?.original"
                        :alt="item?.name"
                    />
                    <span>
                        {{ item.name }}
                    </span>
                </span>
            </li>
        </ul>

        <Button :loading="data.loading"
            class="tertiary size-l"
            :name="t('pages.licenses.providers.load')"
            v-if="data.loading || (data.items.length < data.total)"
            @click="getItems"
        />

        <EmptyLabel
            v-if="!data.items.length && !data.loading"
            class="empty"
            :title="t(`pages.licenses.providers.empty.title`)"
            :description="t(`pages.licenses.providers.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { onMounted, reactive, computed } from 'vue';
    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';

    const emit = defineEmits(['change']);

    const { t } = useI18n();

    const props = defineProps({
        id: { type: [String, null], default: null }
    });

    const data = reactive({
        items: [],
        loading: false,
        skip: 0,
        take: 20,
        total: 0
    });

    const defaultProfileImage = computed(() => {
        return api.licenses().defaultProfileImage;
    });

    const getItems = async () => {
        data.loading = true;

        if (!data.skip) {
            data.items = [];
        }

        const payload = await api.licenses().providers.list({
            skip: data.skip,
            take: data.take
        });


        setTimeout(() => {
            if (payload?.items?.length) {
                data.items.splice(data.items.length, 0, ...(payload.items || []));
            }

            data.skip = data.items.length;
            data.total = payload?.total || 0;

            data.loading = false;
        }, 200);
    };

    const reload = () => {
        data.skip = 0;
        getItems();
    };

    const add = (payload) => {
        data.items.unshift(payload);
        data.total += 1;
    }

    const update = (payload) => {
        if (!payload?.id) {
            return;
        }

        const item = data.items.find(item => item.id === payload.id);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1, payload);
        }
    }

    const remove = (id) => {
        if (!id) {
            return;
        }

        const item = data.items.find(item => item.id === id);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1);
            data.skip -= 1;
            data.total -= 1;
        }
    }

    defineExpose({
        remove,
        add,
        update,
        reload
    });

    onMounted(() => {
        getItems();
    });
</script>

<style lang="scss" scoped>
    @include studio.default-items-list;

    .items-list {
        .items {
            li {
                .item-preview {
                    @include provider-license-list-preview;
                    .wathermark,
                    img {
                        width: 46px;
                        height: 46px;
                    }
                }
            }
        }
    }
</style>