<template>
    <div class="license-provider-info" v-if="!data.loading" v-show="!data.updating">
        <label class="title">{{ t(`pages.licenses.providers.${ props.item?.id ? 'edit' : 'add' }.title`) }}</label>

        <div class="form-line">
            <label>{{ t(`pages.licenses.providers.fields.name`) }}</label>

            <Field
                :placeholder="t(`pages.licenses.providers.fields.name`)"
                :value="data.name"
                :disabled="!user.isTrackCurator"
                class="noerror light with-placeholder"
                @change="onNameChange"
            />
        </div>

        <div class="form-line">
            <label>{{ t(`pages.licenses.providers.fields.description`) }}</label>

            <Textarea
                :placeholder="t(`pages.licenses.providers.fields.description`)"
                :value="data.description"
                class="noerror light with-placeholder"
                @change="onDescriptionChange"
            />
        </div>

        <div class="form-line">
            <label>{{ t(`pages.licenses.providers.fields.placeholder`) }}</label>

            <Field
                :placeholder="t(`pages.licenses.providers.fields.placeholder`)"
                :value="data.placeholder"
                :disabled="!user.isTrackCurator"
                class="noerror light with-placeholder"
                @change="onPlaceholderChange"
            />
        </div>

        <div class="form-line">
            <label>{{ t(`pages.licenses.providers.fields.reg-exp.name`) }}</label>

            <div class="box">
                <Field
                    :placeholder="t(`pages.licenses.providers.fields.reg-exp.name`)"
                    :value="data.regExp"
                    :disabled="!user.isTrackCurator"
                    class="noerror light with-placeholder"
                    @change="onRegExpChange"/>

                <span class="attention">
                    <span>{{ t(`pages.licenses.providers.fields.reg-exp.subattention1`) }}</span>
                </span>
            </div>
        </div>

        <div class="form-line">
            <label>{{ t(`pages.licenses.providers.fields.get-params.name`) }}</label>

            <div class="box">
                <Field
                    :placeholder="t(`pages.licenses.providers.fields.get-params.name`)"
                    :value="data.getParams"
                    :disabled="!user.isTrackCurator"
                    class="noerror light with-placeholder"
                    @change="onGetParamsChange"/>

                <span class="attention">
                    <span>{{ t(`pages.licenses.providers.fields.get-params.subattention1`) }}</span>
                </span>
            </div>
        </div>

        <div class="form-line">
            <label>
                <span>
                    {{ t(`pages.licenses.providers.fields.profile-image`) }}
                </span>
                <span class="attention">
                    {{ t(`pages.licenses.providers.fields.min-profile-image-size`) }}
                </span>
            </label>

            <div class="profile-image-preview">
                <FileUploader 
                    class="upload"
                    icon="upload-img"
                    :acceptedFormats="['image/png', 'image/jpeg', 'image/jpg', 'image/gif', 'image/jfif']"
                    :loading="data.loading"
                    :dropZone="true"
                    :showPreview="true"
                    :preview="data.profileImage?.resized"
                    :zoneSize="{ width: '120px', height: '120px' }"
                    zoneType="square-image"
                    @change="updateProfileImage"
                />
            </div>
        </div>

        <div class="form-line">
            <Toggler
                :name="t(`pages.licenses.providers.fields.showed`)"
                :inline="true"
                :checked="data.showed"
                :disabled="!user.isTrackCurator"
                class="reverse solid"
                @onClick="onShowedChange"
            />
        </div>
 
        <Teleport to="#head-buttons">
            <Button
                v-if="data.id"
                icon="trash"
                :name="t(`pages.licenses.providers.remove.title`)"
                class="size-s tertiary invert"
                @click="remove"
            />

            <Button :name="t(`pages.licenses.providers.${ props.item?.id ? 'edit' : 'add' }.save`)"
                class="size-s secondary"
                :disabled="saveDisabled"
                @click="saveChanges"
            />
        </Teleport>
    </div>
    <div class="loader" v-if="data.loading || data.updating">
        <div>
            <Loader :inside="true" class="tertiary" />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted } from 'vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import Textarea from 'ðŸ”—/components/ui/Textarea.vue';
    import Toggler from 'ðŸ”—/components/ui/Toggler.vue';
    import FileUploader from 'ðŸ”—/components/ui/FileUploader.vue';
    import Loader from 'ðŸ”—/components/ui/Loader.vue';
    import { delay, message } from 'ðŸ”—/utils/utils';
    import stores from '@/stores';

    const modals = stores.modals();
    const user = stores.user();

    const emit = defineEmits(['update', 'add', 'remove', 'edit']);
    const { t } = useI18n();

    const props = defineProps({
        item: { type: [Object, null], default: null }
    });

    const data = reactive({
        id: null,
        name: null,
        description: null,
        placeholder: null,
        profileImage: {},
        newProfileImage: null,
        regExp: null,
        getParams: null,
        showed: false,
        loading: true
    });

    const saveDisabled = computed(() => {
        return !data.name?.trim().length || !data.hasChanges;
    });

    const onNameChange = (e) => {
        data.name = e.target.value?.trim();
        onEdit();
    }

    const onPlaceholderChange = (e) => {
        data.placeholder = e.target.value?.trim();
        onEdit();
    }

    const onDescriptionChange = (e) => {
        data.description = e.target.value?.trim();
        onEdit();
    }

    const onRegExpChange = (e) => {
        data.regExp = e.target.value?.trim();
        onEdit();
    }

    const onGetParamsChange = (e) => {
        data.getParams = e.target.value?.trim();
        onEdit();
    }

    const updateProfileImage = (e) => {
        if (e?.name) {
            data.newProfileImage = e;
            onEdit();
        }
    }

    const onShowedChange = (e) => {
        data.showed = !!e?.value;

        onEdit();
    }

    const onEdit = () => {
        emit('edit');
        data.hasChanges = true;
    }

    const saveChanges = async () => {
        const licenseProviderInfo = {
            name: data.name,
            description: data.description || null,
            placeholder: data.placeholder || null,
            regExp: data.regExp || null,
            getParams: data.getParams || null,
            showed: !!data.showed,
        }

        data.updating = true;

        let payload;

        if (data.id) {
            payload = await api.licenses().providers.update(data.id, licenseProviderInfo);
        } else {
            payload = await api.licenses().providers.add(licenseProviderInfo);
        }

        if (payload?.id && data.newProfileImage) {
            payload = await api.licenses().providers.updateProfileImage(payload.id, data.newProfileImage);
        }

        await delay(200);
        data.updating = false;

        if (payload?.id) {
            message.info(t(`pages.licenses.providers.${ data.id ? 'edit' : 'add' }.done.title`));
            // modals.open('success', {
            //     data: {
            //         timer: 3,
            //         title: t(`pages.licenses.providers.${ data.id ? 'edit' : 'add' }.done.title`),
            //         close: t(`pages.licenses.providers.${ data.id ? 'edit' : 'add' }.done.close`)
            //     },
            //     quietClose: true
            // });
        }

        if (data.id) {
            emit('update', payload);
        } else {
            emit('add', payload);
        }

        data.id = payload.id;
        data.profileImage = payload.profileImage || {};
        data.newProfileImage = null;

        delete data.hasChanges;
    }

    const getItem = async () => {
        data.loading = true;

        const payload = await api.licenses().providers.get(props.item.id);

        data.id = payload?.id;
        data.name = payload?.name;
        data.description = payload?.description;
        data.placeholder = payload?.placeholder;
        data.regExp = payload?.regExp;
        data.getParams = payload?.getParams;
        data.showed = !!payload?.showed;
        data.profileImage = payload?.profileImage || {};

        await delay(200);

        data.loading = false;

        delete data.hasChanges;
    }

    const remove = async () => {
        modals.confirm({
            title: t(`pages.licenses.providers.remove.confirm.title`),
            body: t(`pages.licenses.providers.remove.confirm.description`, { name: data.name }),
            onOk: async () => {
                await api.licenses().providers.remove(data.id);

                emit('remove', data.id);
                
                modals.open('success', {
                    data: {
                        title: t(`pages.licenses.providers.remove.done.title`),
                        close: t(`pages.licenses.providers.remove.done.close`)
                    },
                    quietClose: true
                });
            }
        }); 
    }

    onMounted(() => {
        if (props.item?.id) {
            getItem();
        } else {
            data.loading = false;
        }
    });
</script>

<style lang="scss" scoped>
    .loader {
        @include inside-loader;
    }

    .license-provider-info {
        display: flex;
        flex-direction: column;
        gap: 24px;
        .title {
            font: var(--font-h3);
            font-weight: bold;
            color: var(--dark-500);
        }
    }

    .form-line {
        @include studio.form-line();

        .profile-image-preview {
            position: relative;
            background-size: cover;
            background-position: center;
            border-radius: 10px;
            img {

            }
        }
    }

    // .buttons {
    //     display: flex;
    //     flex-direction: row;
    //     align-items: center;
    //     gap: 8px;
    //     margin-top: 32px;
    // }
</style>
