<template>
    <PageHeader :title="t('pages.licenses.providers.title')" :detail="!!data.item">
        <Button
            v-if="!data.item"
            icon="plus"
            class="size-s"
            :name="t('pages.licenses.providers.add.title')"
            @click="add"
        />

        <IconButton
            v-if="data.item"
            icon="close"
            class="size-s tertiary invert"
            :title="t('pages.licenses.cancel')"
            @click="close"
        />
    </PageHeader>

    <div class="box">
        <LicenseProviders :id="data.item?.id"
            ref="licenseProviders"
            @change="onSelectItem" />

        <div v-if="data.item" class="item-detail">
            <LicenseProviderInfo :item="data.item"
                @add="onAdd"
                @update="onUpdate"
                @remove="onRemove"
                @edit="onEdit"
            />
        </div>
    </div>
</template>

<script setup>
    import { reactive, ref } from 'vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';

    import LicenseProviders from './components/LicenseProviders.vue';
    import LicenseProviderInfo from './components/LicenseProviderInfo.vue';
    
    import stores from '@/stores';
    import { useI18n } from 'vue-i18n';

    const modals = stores.modals();
    const { t } = useI18n();
    const licenseProviders = ref(null)

    const data = reactive({
        item: null
    });

    const onSelectItem = (item) => {
        checkUnsaved(() => {
            close();

            setTimeout(() => {
                data.item = item?.id ? item : null;
            });
        });
    }

    const close = () => {
        checkUnsaved(() => {
            data.item = null;
        });
    }

    const add = () => {
        checkUnsaved(() => {
            close();

            setTimeout(() => {
                data.item = {};
            });
        });
    }

    const checkUnsaved = async (callback) => {
        if (data.hasChanges) {
            modals.confirm({
                title: t(`pages.licenses.providers.unsaved.confirm.title`),
                body: t(`pages.licenses.providers.unsaved.confirm.description`),
                onOk: async() => {
                    delete data.hasChanges;

                    callback();
                }
            }); 
            return;
        }

        callback();
    }

    const onRemove = (id) => {
        licenseProviders.value.remove(id)
        delete data.hasChanges;
        close()
    }

    const onUpdate = (payload) => {
        licenseProviders.value.update(payload)
        data.item = payload;

        delete data.hasChanges;
    }

    const onEdit = () => {
        data.hasChanges = true;
    }

    const onAdd = (payload) => {
        licenseProviders.value.add(payload)
        data.item = payload;

        delete data.hasChanges;
    }
</script>

<style lang="scss" scoped>
    @include studio.default-box;
</style>
